	module.exports = {"data":{"markdownRemark":{"html":"<p>Do you work with interviews or documents in which you have to make meaningful correlations between themes? </p>\n<p>Perhaps you have a lot of data from interviews and you want to know what your respondents associate with a problem. </p>\n<p>Atlas.Ti isn't new, but you may have missed the boat. It provides a visual way of organizing and coding data for this type of research projects, especially those involving qualitative data, or data that can't be understood using traditional statistical methods. </p>\n<p>All the best research, right?</p>\n<p>It's relatively easy to learn, fun to use, and incredibly powerful in the right context.  </p>\n<p>For a few years now, it has had increasingly powerful support for video and audio coding, which means that you can analyze data from almost any source imaginable. Here's a blurb:</p>\n<blockquote class=\"large\">\n\"ATLAS.ti serves as a powerful utility for qualitative analysis, particularly of larger bodies of textual, graphical, audio, and video data. The content or subject matter of these materials is in no way limited to any one particular field of scientific or scholarly investigation. The typical application areas for ATLAS.ti are characterized by a systematic, yet creative approach to analyzing unstructured data.\" \n<p>Visit <a href=\"http://www.atlasti.de/\">the atlas website</a>. </blockquote></p>","frontmatter":{"created_at":"May 21, 2004","path":"/2004/05/21/atlasti-four-star-research-software","title":"Atlas.Ti: Four Star Research Software","tags":["academic","research","software"]}}},"pathContext":{}}

